// user
// login,
// loginApiuserIdstate
import { setToken,getToken,removeToken,setUserName,gettUserName,removeUsername } from "@/util/token";
import { loginApi, reqUserRegist, reqAllDepts,reqLogout} from "../api/index";
const state = {
    // ,
    // 
    deptInfo: {},
    userName:gettUserName(),
    userToken:getToken()
}
const mutations = {

    // 
    USERLOGIN(state,userToken){
        state.userToken = userToken
    },


    //userId,userName
    SETUSERDATA(state,userName){
        state.userName = userName
        console.log(state.userName)
    },

    // deptInforesult.data
    SELECTALLDEPTS(state, deptInfo) {
        state.deptInfo = deptInfo;
        // console.log(deptInfo)
    },

    // user
    USERLOGOUT(state){
        state.userName=''
        state.userToken =''
        removeToken();
        removeUsername();
    }
}
const actions = {

    // (token)
    async userLogin({commit},data){
        let result = await loginApi(data);
        console.log(result)
        if(result.flag == true){
            commit('USERLOGIN',result.data.token)
            // token
            setToken(result.data.token)

            // 
            commit('SETUSERDATA',result.data.user.userName)
            setUserName(result.data.user.userName)
            return 'ok';
        }else{
            return result.msg
        }
    },

    //(userRegist)
    async userRegist({ commit }, data) {
        let result = await reqUserRegist(data);
        // console.log(result)
        if(result.flag == true){
            return 'ok'
        } else {
            return Promise.reject(new Error('faile'))
        }
    },

    // 
    async selectAllDepts({ commit }, data) {
        let result = await reqAllDepts(data);
        if (result.flag == true) {
            commit('SELECTALLDEPTS', result.data)
        }
        // console.log(result)
    },

    // ,data{commit}
    async userLogout({commit}){
        let result = await reqLogout();
        if(result.flag == true){
            commit('USERLOGOUT')
        }
        console.log(result)
    }
}
const getters = {
    // 
    //statestatevuexstate,
    // data:data
    deptInfo(state){
        return state.deptInfo;
    },
    userName(state){
        return state.userName
    }
}

export default {
    state,
    mutations,
    actions,
    getters,
}